 *,
:before,
:after {
    box-sizing: border-box;
    position: relative;
}

body,
html,
.app {
    background: #eee;
    font-family: arial;
    text-align: center;
    width: 100%;
    height: 100%;
    margin: 0;
}

.app {
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    font-size: 0;
    text-align: left;
    background: #333;
    color: #fff;
}
